.component1_st1 {
  min-height: 15vh;
  background-color: #c591cc;
  opacity: calc(3 - var(--scroll) / 100);
}
.component2_st1 {
  min-height: 10vh;
  background-color: #c591cc;
  opacity: calc(5 - var(--scroll) / 100);
}
.component3_st1 {
  min-height: 45vh;
  background-color: #c591cc;
  opacity: calc(10 - var(--scroll) / 100);
}
.component4_st1 {
  min-height: 50vh;
  background-color: #c591cc;
  transform: translateX(max(calc(100% - var(--scroll) / 900 * 100%), 0%));
  opacity: calc(15 - var(--scroll) / 100);
}
.component5_st1 {
  min-height: 50vh;
  background-color: #c591cc;
  transform: translateX(min(calc(-100% + var(--scroll) / 1000 * 100%), 0%));
  opacity: calc(20 - var(--scroll) / 100);
}