.component1_st5 {
  min-height: 10vh;
  background-color: #c591cc;
  opacity: calc(3 - var(--scroll) / 100);
}
.component2_st5 {
  min-height: 45vh;
  background-color: #c591cc;
  opacity: calc(7 - var(--scroll) / 100);
}
.component2_st5 p {
  padding-left: 25%;
  text-align: left;
}
.component3_st5 {
  min-height: 60vh;
  background-color: #c591cc;
  opacity: calc(12 - var(--scroll) / 100);
}
.component3_st5 p {
  padding-left: 22%;
  text-align: left;
}
@media screen and (max-width: 820px) {
  .component2_st5 p {
    width: 100%;
    padding-left: 10%;
    font-size: 0.8em;
  }
}
@media screen and (max-width: 820px) {
  .component3_st5 p {
    width: 100%;
    padding-left: 1%;
    font-size: 0.8em;
  }
}
@media screen and (max-width: 768px) {
  .component2_st5 p {
    width: 100%;
    padding-left: 10%;
    font-size: 0.8em;
  }
}
@media screen and (max-width: 768px) {
  .component3_st5 p {
    width: 100%;
    padding-left: 1%;
    font-size: 0.5em;
  }
}