.component1_st2 {
  min-height: 15vh;
  background-color: #c591cc;
  opacity: calc(3 - var(--scroll) / 100);
}
.component2_st2 {
  min-height: 30vh;
  background-color: #c591cc;
  opacity: calc(6 - var(--scroll) / 100);
}
.component2_st2 ul {
  padding-left: 37%;
  text-align: left;
}
.component3_st2 {
  min-height: 65vh;
  background-color: #c591cc;
  opacity: calc(10 - var(--scroll) / 100);
}
.component3_st2 p {
  padding-left: 35%;
  text-align: left;
}
@media screen and (max-width: 768px) {
  .component2_st2 p {
    padding-left: 0%;
  }
  .component2_st2 ul {
    padding-left: 22%;
  }
}
@media screen and (max-width: 768px) {
  .component3_st2 p {
    width: 100%;
    padding-left: 20%;
  }
}
@media screen and (max-width: 820px) {
  .component2_st2 p {
    padding-left: 0%;
  }
  .component2_st2 ul {
    padding-left: 22%;
  }
}
@media screen and (max-width: 820px) {
  .component3_st2 p {
    width: 100%;
    padding-left: 20%;
  }
}