.component1_st3 {
  min-height: 20vh;
  background-color: #c591cc;
  opacity: calc(3 - var(--scroll) / 100);
}
.component2_st3 {
  min-height: 150vh;
  background-color: #c591cc;
  opacity: calc(17 - var(--scroll) / 100);
}
.component2_st3 p {
  padding-left: 25%;
  text-align: left;
}
.component3_st3 {
  min-height: 200vh;
  background-color: #c591cc;
  opacity: calc(35 - var(--scroll) / 100);
}
.component3_st3 p {
  padding-left: 30%;
  text-align: left;
}
.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.popup-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 4px;
  max-width: 500px;
  width: 90%;
}

@media screen and (max-width: 820px) {
  .component2_st3 p {
    width: 100%;
    padding-left: 10%;
    font-size: 0.8em;
  }
}
@media screen and (max-width: 820px) {
  .component3_st3 p {
    width: 100%;
    padding-left: 15%;
    font-size: 0.8em;
  }
}
@media screen and (max-width: 768px) {
  .component2_st3 p {
    width: 100%;
    padding-left: 10%;
    font-size: 0.8em;
  }
}
@media screen and (max-width: 768px) {
  .component3_st3 p {
    width: 100%;
    padding-left: 20%;
  }
}