.component1_st4 {
  min-height: 10vh;
  background-color: #c591cc;
  opacity: calc(3 - var(--scroll) / 100);
}
.component2_st4 {
  min-height: 15vh;
  background-color: #c591cc;
  opacity: calc(5 - var(--scroll) / 100);
}
.component2_st4 p {
  padding-left: 30%;
  text-align: left;
}
.component3_st4 {
  min-height: 42vh;
  background-color: #c591cc;
  opacity: calc(9 - var(--scroll) / 100);
}
.component3_st4 p {
  padding-left: 27%;
  text-align: left;
}
.component4_st4 {
  min-height: 50vh;
  background-color: #c591cc;
  transform: translateX(max(calc(100% - var(--scroll) / 900 * 100%), 0%));
  opacity: calc(14 - var(--scroll) / 100);
}
.component4_st4 p {
  padding-left: 25%;
  text-align: left;
}
.component5_st4 {
  min-height: 75vh;
  background-color: #c591cc;
  transform: translateX(min(calc(-90% + var(--scroll) / 1000 * 100%), 0%));
  opacity: calc(20 - var(--scroll) / 100);
}
.component5_st4 p {
  padding-left: 25%;
  text-align: left;
}
