.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.root {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #f5f5f5;
  background-image: url('../images/image2.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  padding: 20px;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 80%;
  max-width: 800px;
  background-color: #ffffff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}

.logo {
  width: 100%;
  max-width: 200px;
  margin-bottom: 20px;
}
.title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 16px; /* Adjust according to your theme spacing */
}

.description {
  font-size: 16px;
  margin-bottom: 16px; /* Adjust according to your theme spacing */
}
body {
  padding-top: 60px; /* Adjust this value based on the height of your header */
}

/* TODO responsive design */

/* For mobile phones */
@media (max-width: 480px) {
  .hero strong {
    padding: 10px;
    font-size: 30px; /* Adjust font size for mobile phones */
  }
  .link {
    margin-right: 5px; /* Adjust margin for mobile phones */
  }
  .header {
    padding: 0 5px; /* Adjust padding for mobile phones */
  }
}

/* header */
.header {
  color: #ff1493; /* Deep pink */
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #ffffff;
  z-index: 100;
  padding: 0 20px; /* Add some padding to the sides of the header */
}
.links {
  display: flex;
  gap: 20px; /* Add some gap between the links */
}
.link {
  font-size: 16px;
  color: #3f51b5; /* Adjust according to your theme primary color */
  text-decoration: none;
  margin-right: 50px; /* Add margin to the right side of the link */
}

/* hero */
.hero {
  top:0;
  width: 100%;
  height: 0vh;
  position: relative;
}
.hero strong {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 130px;
  color: aquamarine;
  font-weight: bold;
  display: block;
  width: 100%;
  text-align: center;
  z-index: 1;
}
.hero video {
  position: absolute;
  width: 100%;
  height: auto;
  object-fit: cover;
}
/* responsive design for Tablets */
@media screen and (max-width: 768px) {
  .hero {
    width: 100%;
  }
  .hero strong {
    padding: 15px;
    font-size: 60px; /* Adjust font size for tablets */
  }
  .hero video {
    width: 105%;
    height: auto;
    object-fit: cover;
  }
}
@media screen and (max-width: 820px) {
  .hero {
    width: 100%;
  }
  .hero strong {
    padding: 15px;
    font-size: 60px; /* Adjust font size for tablets */
  }
  .hero video {
    width: 100%;
    height: auto;
    object-fit: cover;
  }
}
@media screen and (max-width: 375px) {
  .hero {
    width: 100%;
  }
  .hero strong {
    padding: 15px;
    font-size: 60px; /* Adjust font size for tablets */
  }
}

/* body */
.road {
  flex-direction: column;
  justify-content: center;
  width: 100%;
  padding: 50px 20px;
  text-align: center;
  color: #000000;

  position: sticky;
  top: 0;
  height: var(--page-height); /* Use this instead if you want to use the same value as the page height */
  overflow: hidden;

}
.road .title {
  font-size: 3em;
  margin-bottom: 20px;
}
.road .description {
  font-size: 1.5em;
  margin-bottom: 20px;
}
.road .road-button {
  font-size: 1.2em;
  padding: 10px 20px;
  background-color: #ff6347;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.road .link {
  font-size: 1em;
  color: #3f51b5;
  text-decoration: none;
}
@media screen and (max-width: 768px) {
  .road {
    width: 100%;
    font-size: small;
  }
}
@media screen and (max-width: 820px) {
  .road {
    width: 100%;
    font-size: small;
  }
}
/* Modal (road) */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}
.modal {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  width: 50%; /* Set the width to 50% of the viewport */
  height: 75%; /* Set the height to 75% of the viewport */
  max-width: 750px; /* Set the max-width to 750px */
  max-height: 90%; /* Set the max-height to 90% of the viewport */
  overflow: auto; /* Add a scrollbar if the content is too long */
}
.modal button {
  font-size: 1.2em;
  padding: 10px 20px;
  background-color: #ff6347;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
}
.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}
.grid-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.grid-item img:hover {
  cursor: pointer;
}
/* responsive design for Tablets */
@media screen and (max-width: 820px) {
  .grid {
    grid-template-columns: repeat(1, 1fr);
    gap: 10px;
  }
}
@media screen and (max-width: 768px) {
  .grid {
    grid-template-columns: repeat(1, 1fr);
    gap: 10px;
  }
}
.scroll-to-top {
  position: fixed;
  top: 80px;
  left: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  color: white;
  border-radius: 50%;
  cursor: pointer;
  opacity: 0.7;
  transition: opacity 0.3s ease;
  z-index: 100;
}
.scroll-to-top:hover {
  opacity: 1;
  z-index: 101;
}
.scroll-to-top-button {
  font-size: 1.2em;
  padding: 10px 20px;
  background-color: #ff6347;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.navigation-buttons {
  position: fixed;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  pointer-events: none;
  z-index: 101;
  opacity: 0;
  transition: opacity 0.3s;

}
.navigation-buttons .button-container {
  background-color: rgba(0, 0, 0, 0.5);
  width: 10%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.navigation-buttons button {
  pointer-events: all;
  font-size: 1.2em;
  padding: 0px 0px;
  background-color: #00000000;
  color: rgb(255, 255, 255);
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.navigation-buttons.show {
  opacity: 1;
}
/* Footer */
.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #333; /* Set the background color */
  color: white; /* Set the text color */
  text-align: center;
  opacity: 0; /* Set initial opacity to 0 */
  transition: opacity 0.5s ease-in-out; /* Add transition effect */
}
.footer.show {
  opacity: 1; /* Set opacity to 1 when the element is shown */
}
.about-us {
  color: #ffffff; /* Set the text color to white */
  padding: 20px; /* Add some padding */
}

@media screen and (max-width: 768px) {
  .footer {
    width: 100%;
  }
}